@import url(https://fonts.googleapis.com/css?family=Montserrat:500,700);@import url(https://fonts.googleapis.com/css?family=Raleway:500);
body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.container {
  max-width: 1225px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 50px 15px;
  margin: 0 auto;
  overflow: hidden;
  min-height: calc(100vh - 376px);
}






















.preview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.preview:after {
    content: "";
    -webkit-box-flex: 1;
        -ms-flex: auto;
            flex: auto;
    width: 30%;
    max-width: 30%;
}
@media screen and (max-width: 700px) {
.preview {
      width: 100%;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
}
.preview-item {
  max-width: 270px;
  width: 30%;
  border: 1px solid #e9e9e9;
  margin: 30px 0;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.preview-item:hover {
    -webkit-box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.15);
            box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.15);
}
.preview-item:hover .preview__img:after {
      opacity: 0.7;
}
@media screen and (max-width: 1000px) {
.preview-item {
      width: 48%;
}
}
@media screen and (max-width: 700px) {
.preview-item {
      width: auto;
}
}
.preview__img {
  height: 182px;
  position: relative;
  overflow: hidden;
}
.preview__img:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 120px;
    bottom: 0;
    left: 0;
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(20, 21, 25, 0.5)), to(rgba(20, 21, 25, 0)));
    background: linear-gradient(to top, rgba(20, 21, 25, 0.5) 0%, rgba(20, 21, 25, 0) 100%);
    z-index: 1;
}
.preview__img:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #1cb8ff;
    opacity: 0;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.preview__img img {
    width: 100%;
    height: auto;
}
.preview__city {
  position: absolute;
  bottom: 13px;
  left: 24px;
  color: #fff;
  font-size: 13px;
  line-height: 22px;
  font-weight: 500;
  z-index: 5;
}
.preview-info {
  padding: 15px 20px;
}
.preview-bottom {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.preview__title {
  color: #33363e;
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;
  margin-bottom: 16px;
}
.preview__category {
  color: #a6a9af;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  font-size: 13px;
  line-height: 22px;
}
.preview__price {
  color: #282d3b;
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
}

.filters {
  width: 240px;
  float: left;
  margin-right: 15px;
  padding-right: 30px;
}
@media screen and (max-width: 700px) {
.filters {
      width: 100%;
}
}
.filter-item {
  margin-bottom: 60px;
}
.filter__title {
  display: inline-block;
  text-transform: uppercase;
  color: #33363e;
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  margin-bottom: 30px;
}
.filter-list__item {
  margin-bottom: 18px;
}
.filter-list__item:last-child {
    margin-bottom: 0;
}
input[type=checkbox].filter-checkbox {
  position: absolute;
  z-index: -1000;
  left: -1000px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
input[type=checkbox].filter-checkbox + .filter-label {
  padding-left: 23px;
  height: 18px;
  display: inline-block;
  line-height: 18px;
  background-repeat: no-repeat;
  background-position: 0 0;
  font-size: 15px;
  vertical-align: middle;
  cursor: pointer;
}
input[type=checkbox].filter-checkbox:checked + .filter-label {
  background-position: 0 -18px;
}
.filter-label {
  background-image: url(data:image/png;base64,);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
}
.filter-label__count {
  color: #a6a9af;
  font-size: 15px;
  line-height: 18px;
}
.filter-select {
  border: 1px solid #dee0e1;
  width: 100%;
  overflow: hidden;
  background: url(data:image/png;base64,) no-repeat right center;
  height: 40px;
}
.filter-select select {
    border: 0;
    outline: none;
    background: transparent;
    border: none;
    padding: 5px;
    font-size: 16px;
    height: 40px;
    width: calc(100% + 20px);
}
.filter-bottom {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 20px;
}
.filter-button {
  color: #fff;
  font-size: 14px;
  border: none;
  outline: none;
  cursor: pointer;
  text-align: center;
  width: 100px;
  height: 35px;
  line-height: 1;
  background: #1cb8ff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.filter-button:hover {
    background: #009fe8;
}
.vue-slider-component .vue-slider-process {
  background: #1cb8ff;
}
.vue-slider-component .vue-slider-dot {
  width: 15px;
  height: 15px;
  border: 1px solid #1cb8ff;
}
.vue-slider-component .vue-slider {
  border-radius: 0;
}
